/* montserrat-300 - latin_cyrillic */
@font-face {
        font-family: 'defaultFont';
        font-style: normal;
        font-weight: 300;
        src: local('Montserrat Light'), local('Montserrat-Light'),
             url('../fonts/montserrat-v14-latin_cyrillic-300.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
             url('../fonts/montserrat-v14-latin_cyrillic-300.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
      
